import {request} from "./request";

export function getCheck(params) {  //登录用，判断userid和password是否正确
  return request({
    url: '/getCheck',
    method:'post',
    data:params
  })
}
export function getSession() {  //获取session
  return request({
    url: '/getSession',
    method:'post'
  })
}
export function checkPwd(params) { //检查密码，修改密码用
  return request({
    url: '/checkPwd',
    method:'post',
    data:params
  })
}

export function logOut() {//登出
  return request({
    url: '/logOut',
    method:'get'
  })
}

export function getYuyue() {  //获取当前医生所预约、需要诊断的全部病人(排队中+诊断中)，不包括已完成治疗的病人。
  return request({
    url: '/yuyue',
    method:'get'
  })
}

