import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import { Layout }  from "../Layout"; // 页面整体布局
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path:'/login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue')
  },
  {
    path:'/404',
    component: () => import(/* webpackChunkName: "about" */ '../views/404.vue')
  },

  { //first router
		path: '/index',
		component: Layout,
		children:[ 
			{
				path:'historycus', 
        component: () => import('../views/HistoryCus.vue'),
      },

      {
				path:'main', 
        component: () => import('../Layout/Main.vue'),
      },
      {
				path:'changepwd', 
        component: () => import('../views/changepwd.vue'),
      },
      {
				path:'patient', 
        component: () => import('../views/patient.vue'),
      },//下面是admin的
      {
				path:'customerManage', 
        component: () => import('../views/admin/customerManage.vue'),
      },
      {
				path:'doctorManage', 
        component: () => import('../views/admin/doctorManage.vue'),
      },
      {
				path:'lunbotu',
        component: () => import('../views/admin/lunbotu.vue'),
      },
      {
				path:'newsAdd',
        component: () => import('../views/admin/newsAdd.vue'),
      },
      {
				path:'newsManage',
        component: () => import('../views/admin/newsManage.vue'),
      },

      {
				path:'paibanDoctor',
        component: () => import('../views/admin/paibanDoctor.vue'),
      },
      {
				path:'addDoctor',
        component: () => import('../views/admin/addDoctor.vue'),
      },
      {
				path:'deleteDoctor',
        component: () => import('../views/admin/deleteDoctor.vue'),
      }
		]
  },
  {
    path: '/home',
    component: Home
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
