<template>
  <div class="container" v-if="user.doctorid">
    <div class="content">
      <Header :cuser='user'></Header>
      <Aside :cuser='user'></Aside>
      <router-view :cuser='user' class="routerView"></router-view>
    </div>
  </div>
  <div v-else>
    <page404/>
  </div>
</template>

<script>
import Header from "./Header.vue";
import Aside from "./Aside.vue";
import page404 from "../views/404.vue";
import { getSession} from "../network/data";
export default {
  data() {
    return {
      user: ''
    }
  },
  created() {
    //this.user=this.$store.state.session
    this.getSession();
  },
  components: {
    Header,
    Aside,
    page404
  },
  methods: {
    getSession() {
      getSession().then(res => {
        this.user = res;
        console.log(this.user)
      });
    },
  },
  
};
</script>

<style>
.routerView {
  position: absolute;
  left: 20%;
  top: 12%;
  padding: 0;
  margin: 0;
  
}
a {
  text-decoration: none;
}

</style>